'use client';

import React, { useState } from 'react';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Badge } from '@/components/ui/badge';

interface ChartDataItem {
  mes: string;
  receitas: number;
  despesas: number;
}

interface ChartAreaInteractiveProps {
  data?: ChartDataItem[];
  loading?: boolean;
  error?: string | null;
}

const ChartAreaInteractive: React.FC<ChartAreaInteractiveProps> = ({
  data = [],
  loading = false,
  error = null
}) => {
  const [selectedMonth, setSelectedMonth] = useState<string>('all');

  const formatCurrency = (value: number): string => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(value);
  };

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-white p-3 border rounded-lg shadow-lg">
          <p className="font-medium">{`Mês: ${label}`}</p>
          <p className="text-green-600">
            Receitas: {formatCurrency(payload[0]?.value || 0)}
          </p>
          <p className="text-red-600">
            Despesas: {formatCurrency(payload[1]?.value || 0)}
          </p>
        </div>
      );
    }
    return null;
  };

  const filteredData = selectedMonth === 'all' 
    ? data 
    : data.filter(item => item.mes === selectedMonth);

  const monthOptions = ['all', ...data.map(item => item.mes)];

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Receitas vs Despesas - Últimos 6 Meses</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="h-[400px] flex items-center justify-center">
            <div className="text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
              <p className="text-gray-600">Carregando dados...</p>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Receitas vs Despesas - Últimos 6 Meses</CardTitle>
        </CardHeader>
        <CardContent>
          <Badge variant="destructive" className="w-full justify-center py-4">
            Erro ao carregar dados do gráfico: {error}
          </Badge>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between">
        <CardTitle>Receitas vs Despesas - Últimos 6 Meses</CardTitle>
        <Select value={selectedMonth} onValueChange={setSelectedMonth}>
          <SelectTrigger className="w-40">
            <SelectValue placeholder="Selecionar mês" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">Todos os meses</SelectItem>
            {data.map(item => (
              <SelectItem key={item.mes} value={item.mes}>
                {item.mes}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={400}>
          <AreaChart data={filteredData}>
            <defs>
              <linearGradient id="colorReceitas" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#10b981" stopOpacity={0.8}/>
                <stop offset="95%" stopColor="#10b981" stopOpacity={0.2}/>
              </linearGradient>
              <linearGradient id="colorDespesas" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#ef4444" stopOpacity={0.8}/>
                <stop offset="95%" stopColor="#ef4444" stopOpacity={0.2}/>
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="mes"
              tick={{ fontSize: 12 }}
            />
            <YAxis 
              tick={{ fontSize: 12 }}
              tickFormatter={formatCurrency}
            />
            <Tooltip content={<CustomTooltip />} />
            <Area
              type="monotone"
              dataKey="receitas"
              stackId="1"
              stroke="#10b981"
              fill="url(#colorReceitas)"
              strokeWidth={2}
            />
            <Area
              type="monotone"
              dataKey="despesas"
              stackId="2"
              stroke="#ef4444"
              fill="url(#colorDespesas)"
              strokeWidth={2}
            />
          </AreaChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );
};

export default ChartAreaInteractive;