'use client';

import React, { useState, useEffect } from 'react';
import SectionCards from '@/components/section-cards';
import ChartAreaInteractive from '@/components/chart-area-interactive';

interface Metrics {
  totalDespesas: number;
  totalReceitas: number;
  saldoAtual: number;
  totalTransacoes: number;
}

interface ChartDataItem {
  mes: string;
  receitas: number;
  despesas: number;
}

const DashboardPage = () => {
  const [metrics, setMetrics] = useState<Metrics | null>(null);
  const [chartData, setChartData] = useState<ChartDataItem[]>([]);
  const [loadingMetrics, setLoadingMetrics] = useState(true);
  const [loadingChart, setLoadingChart] = useState(true);
  const [metricsError, setMetricsError] = useState<string | null>(null);
  const [chartError, setChartError] = useState<string | null>(null);

  const fetchMetrics = async () => {
    try {
      setLoadingMetrics(true);
      setMetricsError(null);
      
      const response = await fetch('/api/dashboard/metrics');
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      setMetrics(data);
    } catch (error) {
      console.error('Error fetching metrics:', error);
      setMetricsError(error instanceof Error ? error.message : 'Erro desconhecido');
    } finally {
      setLoadingMetrics(false);
    }
  };

  const fetchChartData = async () => {
    try {
      setLoadingChart(true);
      setChartError(null);
      
      const response = await fetch('/api/dashboard/chart-data');
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      setChartData(data);
    } catch (error) {
      console.error('Error fetching chart data:', error);
      setChartError(error instanceof Error ? error.message : 'Erro desconhecido');
    } finally {
      setLoadingChart(false);
    }
  };

  useEffect(() => {
    fetchMetrics();
    fetchChartData();
  }, []);

  return (
    <div className="container mx-auto p-6">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Dashboard Financeiro
        </h1>
        <p className="text-gray-600">
          Visão geral das suas finanças pessoais
        </p>
      </div>

      {/* Cards Section */}
      <SectionCards 
        metrics={metrics || undefined}
        loading={loadingMetrics}
        error={metricsError}
      />

      {/* Chart Section */}
      <ChartAreaInteractive
        data={chartData}
        loading={loadingChart}
        error={chartError}
      />

      {/* Refresh Button */}
      <div className="mt-8 flex justify-center">
        <button
          onClick={() => {
            fetchMetrics();
            fetchChartData();
          }}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          disabled={loadingMetrics || loadingChart}
        >
          {(loadingMetrics || loadingChart) ? 'Carregando...' : 'Atualizar Dados'}
        </button>
      </div>
    </div>
  );
};

export default DashboardPage;